import React from 'react'

import './Achievements.css'

import icon from '../assets/icon.svg'
import icon1 from '../assets/icon-1.svg'
import icon2 from '../assets/icon-2.svg'
import icon3 from '../assets/icon-3.svg'

export default function Achievements() {
    return (
        <section className="Achievements">
            <div className="card">
                 <figure>
                     <img src={icon} alt="icon" />
                 </figure>
                 <div>
                     <h2>60</h2>
                     <hgroup>

                     <h4>Statisfied</h4>
                     <h3>Client</h3>
                     </hgroup>
                 </div>
            </div>
            <div className="card">
                 <figure>
                     <img src={icon1} alt="icon" />
                 </figure>
                 <div>
                     <h2>10</h2>
                     <hgroup>
                     <h4>Worldwide</h4>
                     <h3>Branches</h3>
                     </hgroup>
                 </div>
            </div>
            <div className="card">
                 <figure>
                     <img src={icon2} alt="icon" />
                 </figure>
                 <div>
                     <h2>80</h2>
                     <hgroup>

                     <h4>Total</h4>
                     <h3>Projects</h3>
                     </hgroup>
                 </div>
            </div>
            <div className="card">
                 <figure>
                     <img src={icon3} alt="icon" />
                 </figure>
                 <div>
                     <h2>24</h2>
                     <hgroup>

                     <h4>Work</h4>
                     <h3>Finished</h3>
                     </hgroup>
                 </div>
            </div>
        </section>
    )
}
