import React from 'react'

import './About.css'

import image from '../assets/about_part_img.webp'
import houseIcon from '../assets/house-icon.svg'

export default function About() {
    return (
        <section className="About">
            <figure>  
                <img src={image} alt="about-us" />  
            </figure>

            <div>
          
            <h2>Your Vision, Our Expertise.</h2>
            <p>MBC Greenpoint is the brainchild of a group of construction professionals who
have acquired the necessary construction skills and expertise in their professional
careers to offer construction and consultancy services to a range of clients.</p>
            <div className="card">
                <figure>

                <img src={houseIcon} alt="house plans"/>
                </figure>
                <div>
                    <h3>Certified Company</h3>
                    <p>The team members are carefully resourced having the required skills and
equipment to ensure that the demands of that specific project are met. </p>
                </div>
            </div>
            <div className="card">
            <figure>

<img src={houseIcon} alt="house plans"/>
</figure>
                <div>
                    <h3>Employee Experience</h3>
                    <p>The team members are carefully resourced having the required skills and
equipment to ensure that the demands of that specific project are met. </p>
                </div>
            </div>
            </div>
        </section>
    )
}
