import React from 'react'

import './Blog.css'

import blog1 from '../assets/blog_1.webp'
import blog2 from '../assets/blog_2.webp'
import blog3 from '../assets/blog_3.webp'
import commentIcon from '../assets/comment-icon.svg'
import heartIcon from '../assets/heart-icon.svg'

export default function Blog() {
    return (
        <section className="Blog">
            <h2>Recent News</h2>
            <div>
            <div className="card">
                <figure>
                    <img src={blog1} alt="blog" />
                </figure>
                <ul>
                    <li><img src={commentIcon} alt="comments" /><span>2 comments</span></li>
                    <li><img src={heartIcon} alt="like" /><span>2k likes</span></li>
                </ul>
                <h4>Our two firmament called us kind in face midst</h4>
                <a href="#more">Read More</a>
            </div>
            <div className="card">
                <figure>
                    <img src={blog2} alt="blog" />
                </figure>
                <ul>
                    <li><img src={commentIcon} alt="comments" /><span>2 comments</span></li>
                    <li><img src={heartIcon} alt="like" /><span>2k likes</span></li>
                </ul>
                <h4>Our two firmament called us kind in face midst</h4>
                <a href="#more">Read More</a>
            </div>
            <div className="card">
                <figure>
                    <img src={blog3} alt="blog" />
                </figure>
                <ul>
                    <li><img src={commentIcon} alt="comments" /><span>2 comments</span></li>
                    <li><img src={heartIcon} alt="like" /><span>2k likes</span></li>
                </ul>
                <h4>Our two firmament called us kind in face midst</h4>
                <a href="#more">Read More</a>
            </div>
            </div>
        </section>
    )
}
